@import url(https://fonts.googleapis.com/css2?family=Courgette&family=Open+Sans:wght@400;500;600;700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  width: 100%;
  height: 100vh;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #0d0d0d;
  background-color: #fff;
}


.headerWrapper {
    width: 100%;
    height: 50px;
    color: #0063df;
    background-color: #ffffff;   
    box-shadow: 0px -14px 32px #8d8d8d;
}
.headerInner {
    margin: 0 auto;
    padding: 0px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}
.logo > img{
    height: 60px;
    width: 60px;
    object-fit: contain;
}
.searchBar {
    padding: 5px;
}
.inputWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}
.inputField > input {
    padding: 5px;
    height: 30px;
    width: 260px;
    border: 1px solid #0063df;
    border-right: 0;
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
}
.inputField > input:focus{
    outline: none;
}
.searchBtnWrapper > a {
    position: relative;
    display: block;
    text-align: center;
    font-size: 16px;
    height: 30px;
    width: 30px;  
    color: #fff;  
    background-color: #0063df;
    border: 1px solid #0063df;
}
.searchBtnWrapper > a >svg{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
.navBar > ul {
    list-style: none;
    font-size: 14px;
}
.navBar > ul > li {
    display: inline-block;
    padding: 5px 10px;
    cursor: pointer;
}
.navBar > ul > li:hover{
    background-color: #0063df;
    transition: .3s all;
    border-radius: 2px;
}
.navBar > ul > li:hover > a{
    color: #fff;
}
.navBar > ul > li > a {
    text-decoration: none;
    color:#0063df;
    font-weight: 500;
    font-size: 15px;
    line-height: 1.5;
}
.toggleNavBar{
    display: none;
    font-size: 20px;
    opacity: 0.9;
    cursor: pointer;
}
@media screen and (max-width:780px) {
    .toggleNavBar{
        display: block;
    }
    .navBar{
        display: none;
    }
    .navBar.active{
        display: block;
    }
    .navBar .navItem {
        position: absolute;
        top: 50px;
        right: 0;
        width: 200px;
        color:#0063df;
        background-color: #fff;        
        border: 1px solid #ccc; 
        border-top: 0;
    }
    .navList {
        display: block;
        text-align: center;
        width: 100%;  
    }
    .navList:not(:last-child){
        border-bottom: 1px solid #ddd; 
    }
}
.footerWrapper {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding: 10px 20px;
    background-color: #000;
    color: #bbb;
}
.containerWrapper{
    height: calc(100vh - 89px);
    margin: 0 auto;
    background-color: rgb(255, 255, 255);
    overflow: auto;
}
.heroSection{
    position: relative;
    height: 400px;
    width: 100%;
    background-image: url(/codemastery/static/media/herosectionbg.8fc70212.jpg);
    background-position: bottom right;
    background-size: cover;
    background-repeat: no-repeat;
}
.heading {
    position: absolute;
    top: 47px;
    right: 200px;
    padding: 18px;
    color: #fff;
    font-size: 20px;
    font-family: cursive;
}
.headerImage{
    position: absolute;
    top: 150px;
    right: 350px;
}
